@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
/* ISSUES LIST */
.issues {
  height: 100%;
  width: 100%;
}

.issues-title {
  display: flex;
  margin: 1.5rem 0;
  justify-content: center;
}

.issues-title h1 span {
  color: var(--success-color);
}

.issues-list {
  width: 100%;
  padding: 0;
  list-style-type: none;
}

.issues li {
  border-top: 0.1rem solid var(--primary-color-lighter);
}

.issues li a {
  text-decoration: none;
  color: #203040;
  font-weight: bold;
}

.issue {
  padding: 2rem;
  display: flex;
  flex-flow: wrap;
  flex-direction: row;
  height: 100%;
}

.issue div {
  margin-right: 2rem;
}

.issue:hover {
  background-color: var(--hover-color);
}

.issue-status {
  display: flex;
  align-items: flex-start;
  color: var(--success-color);
}

.issue-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 2rem;
}

.issue-labels {
  display: flex;
  flex-flow: wrap;
  flex-direction: row;
  align-items: center;
  font-size: 1.5rem;
}

.issue-label {
  padding: 0.5rem 2rem;
  border-radius: 2rem;
  margin: 0.2rem 0;
}

/* LOADING */
.loading {
}

.loader,
.loader:before,
.loader:after {
  background: var(--primary-color-lightest);
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: var(--primary-color-dark);
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

/* SEARCHBAR */
.search {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
}

.searchTerm {
  padding: 1rem 2rem;
  width: 100%;
  border: none;
  border-bottom: 0.5rem solid var(--primary-color-dark);
  outline: none;
  color: var(--primary-color-lighter);
  font-weight: bold;
  font-size: 2rem;
  height: 4rem;
}
.searchTerm::-webkit-input-placeholder {
  color: var(--primary-color);
}
.searchTerm:-ms-input-placeholder {
  color: var(--primary-color);
}
.searchTerm::-ms-input-placeholder {
  color: var(--primary-color);
}
.searchTerm::placeholder {
  color: var(--primary-color);
}

.searchTerm:focus {
  color: var(--primary-color-dark);
}

/* WELCOME */
.welcome h3 {
  color: var(--primary-color);
}

/* IMPORT FONTS */

/* VARIABLES */
:root {
  --primary-color: #616161;
  --primary-color-lighter: #e0e0e0;
  --primary-color-lightest: #f5f5f5;
  --primary-color-dark: #212121;
  --success-color: #689f38;
  --hover-color: #f2f4ff;
}

/* BASIC RESETS */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body,
#root {
  height: 100vh;
  margin: 0;
}
body,
input,
select,
button,
textarea {
  font-family: Montserrat, Helvetica, Arial, sans-serif;
  font-size: 1.6rem;
}

/* LAYOUT GRID */
.grid-container {
  display: grid;
  grid-template-areas:
    'header'
    'main'
    'footer';
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-columns: 1fr;
  height: 100%;
}

header {
  grid-area: 'header';
}

main {
  grid-area: 'main';
}

footer {
  grid-area: 'footer';
  background-color: #212121;
  background-color: var(--primary-color-dark);
  color: #f5f5f5;
  color: var(--primary-color-lightest);
  display: flex;
  justify-content: center;
  align-items: center;
}

/*  MAIN CONTENT */
.content {
  height: 100%;
}

.main {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
}

